.cross-list { font-size: 0; text-align: center; }

@media (max-width: 992px) { .cross-list { display: flex; flex-direction: column; } }

@media (min-width: 768px) { .cross-list { flex-wrap: wrap; flex-direction: row; justify-content: space-around; } }

@media (min-width: 992px) { .cross-list { overflow: hidden; white-space: nowrap; } }

.cross-list .large_img, .cross-list .small_img { position: relative; border-top: 1px solid #d1d1d1; border-bottom: 1px solid #d1d1d1; vertical-align: top; margin-bottom: 9px; box-sizing: content-box; }

@media (max-width: 992px) { .cross-list .large_img, .cross-list .small_img { flex: 1; } }

@media (min-width: 768px) { .cross-list .large_img, .cross-list .small_img { display: inline-block; max-width: 367px; border-left: 1px solid #d1d1d1; border-right: 1px solid #d1d1d1; } }

@media (min-width: 992px) { .cross-list .large_img, .cross-list .small_img { overflow: hidden; display: inline-block; margin-bottom: 0; } }

@media (max-width: 992px) { .cross-list .large_img:nth-of-type(1) { -webkit-order: 1; order: 1; } }

@media (max-width: 992px) { .cross-list .large_img:nth-of-type(3) { -webkit-order: 2; order: 2; } }

@media (max-width: 992px) { .cross-list .small_box { width: 100%; -webkit-order: 3; order: 3; justify-content: space-around; } }

@media (min-width: 768px) and (max-width: 992px) { .cross-list .small_box { display: flex; } }

@media (min-width: 992px) { .cross-list .small_box { display: inline-block; margin: 0 15px; } }

@media (min-width: 992px) { .cross-list .small_img { display: block; }
  .cross-list .small_img:first-child { margin-bottom: 12px; } }

.cross-list .inner_box { position: absolute; bottom: 0; left: 0; right: 0; padding-top: 19px; background-color: rgba(255, 255, 255, 0.8); }

@media (min-width: 992px) { .cross-list .inner_box { transform: translateY(100%); transition: transform .23s ease-in; }
  .cross-list .small_img:hover .inner_box, .cross-list .large_img:hover .inner_box { transform: translateY(0); } }

.cross-list .inner_box strong { font-size: 16px; color: #333; text-align: center; }

.cross-list .inner_box p { margin: 8px 0 14px; padding: 0 9px; font-size: 16px; color: #666; line-height: 20px; }

.cross-list img { vertical-align: top; }

@media (max-width: 768px) { .cross-list img { width: 100%; height: auto; } }
